import { Editor } from "@tiptap/core"
import { defaultExtensions } from '@tiptap/starter-kit'


export class DotEditor {

    editor: Editor;
    element: HTMLDivElement;

    constructor() {
        console.log('init');
        this.element = document.querySelector('.dot-editor-host') as HTMLDivElement;
        this.editor = new Editor({
            element: this.element,
            extensions: [
                ...defaultExtensions()
            ],
            content: '<p>Dot Editor Initial Content</p>',
        });
    }


     public dotActions = {
        toggleBold: () => {
            this.editor.chain().focus().toggleBold().run();
        },
        toggleItalic: () => {
            this.editor.chain().focus().toggleItalic().run()
        },
        toggleStrike: () => {
            this.editor.chain().focus().toggleStrike().run();
        },
        toggleHeading: (level: number) => {
            this.editor.chain().focus().toggleHeading({ level: level }).run()
        },
        undo: () => {
            this.editor.chain().focus().undo().run()
        },
        redo: () => {
            this.editor.chain().focus().red().run()
        },
    };

}

export const dotTest = new DotEditor();









